<template>
    <div>
        <h2 class="product-heading"
        v-show="details.id === undefined">Please select a product</h2>
        <div class="list-item"
        v-show="details.id !== undefined">
            <div class="img-app">
                <img :src="details.image" alt="image" width="100%" height="100%">
            </div>
            <p>{{ '$' + details.price }}</p>
            <p class="product-category">{{ details.category }}</p>
            <h3>{{ details.title }}</h3>
            <p>{{ details.description }}</p>
            <div class="button-app">
                <button v-if="details.active === true" class="button button-cart" @click="productInCart(id, prodArr)">В корзину</button>
                <button v-else-if="details.active === false" class="button button-remove" @click="deleteInCart(id)">Удалить</button>
            </div>
        </div>
    </div>  
</template>

<script>
export default {
    props: ['details', 'id', 'prodArr', 'cartProduct'],
    inject: ['productInCart', 'deleteInCart'],
}
</script>

<style>
.product-heading {
    margin-top: 2rem;
    color: red;
}
.product-category {
    text-transform: capitalize;
    background: grey;
    border-radius: 16px;
    padding: 0.2rem 0.3rem;
}
</style>