<template>
  <div>
    <ul class='tabs__header'>
      <li class="tabs__header-item"
      v-for="(tab, index) in tabs"
      :key="tab.title"
      @click='selectTab(index)'>
        {{ tab.title }} 
      </li>
    </ul>
    <slot></slot>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selectedIndex: 0,
      tabs: [],
    }
  },
  props: [
    'cart',
    'selected',
  ],
  inject: ['productFetch', 'moreDetails'],
  created () {
    this.tabs = this.$children;
  },
  mounted () {
    this.selectTab(0)
  },
  methods: {
    selectTab(i) {
      this.selectedIndex = i;
      this.tabs.forEach((tab, index) => {
      tab.isActive = (i === index)
      });
      if (i === 0) {
        this.productFetch();
      }
    },
  }
}
</script>

<style>
.tabs__header {
    list-style-type: none;
    width: 70vw;
    display: flex;
    justify-content: space-around;
}
.tabs__header-item {
    text-transform: capitalize;
    cursor: pointer;
}
.tabs__header-item:hover {
  color: grey;
}
</style>