<template>
    <li class="list-item">
        <div class="img-app">
            <img :src="image" alt="image" width="100%" height="100%">
        </div>
        <p>{{ '$' + price }}</p>
        <h3>{{ title }}</h3>
        <p>{{ descriptionLimited }}</p>
        <div class="button-app">
            <button class="button button-details" @click="moreDetails(id)">Подробнее</button>
            <button v-if="active === true" class="button button-cart" @click="productInCart(id, prodArr)">В корзину</button>
            <button v-else-if="active === false" class="button button-remove" @click="deleteInCart(id)">Удалить</button>
        </div>
    </li>
</template>

<script>
export default {
    props: [
    'title', 
    'description', 
    'image', 
    'price', 
    'category',
    'id',
    'active',
    'prodArr',
    'cart'
    ],
    inject: ['productInCart', 'deleteInCart', 'moreDetails'],
    computed: {
        descriptionLimited() {
            if (this.description.length > 185) {
                return this.description.slice(0,185) + '...';
            } else {
                return this.description;
            }
        },
    },
    
}
</script>

<style>
.list-item {
    min-height: 35rem;
    width: 30vw;
    list-style-type: none;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 0px 1px 4px rgb(0 0 0 / 15%);
    border-radius: 16px;
}
.img-app {
    height: 15rem;
    width: 12rem;
}
.button {
   width: 10vw;
   height: 2rem;
   cursor: pointer;
}
.button-details {
    background: white;
    border: 1px blue solid;
    border-radius: 3px;
    color: blue;
}
.button-cart {
    background: green;
    color: white;
    border: none;
}
.button-remove {
    background: red;
    color: white;
    border: none;
}
</style>