<template>
  <div id="app">
    <section class="header">
      <div>
        <h3 class="logo">mecle test</h3>
      </div>
      <nav>
        <tabs
        ref="tabsId"
        :selected="selected">
          <tab class="tab"
          title="home"
          :cart="cart">
            <home-resources
            :cart="cart"
            :productArr="productArr">
            </home-resources>
          </tab>
          <tab title="category">
            <select @change="chengeCategory" @click="fetchCategory">
              <option disabled selected value="">
              Select a category
              </option>
              <option
              v-for="cat in arrCategory"
              :key="cat.id"
              :value="cat">
              {{ cat }}
              </option>
            </select>
            <category-resources
            :mainArrCategory="mainArrCategory">
            </category-resources>
          </tab>
          <tab title="product">
            <the-product
            :details="details"
            :id="details.id"
            :prodArr="productArr"
            :cartProduct="cartProduct">
            </the-product>
          </tab>
        </tabs>
      </nav>
      <div>
          <button class="button-nav" @click="closeCart()"><font-awesome-icon class="icon" :icon="myIcon"></font-awesome-icon></button>
      </div>
      <!-- <teleport to="body"> -->
      <cart-alert v-if="cartActive">
        <ul class="list-cart">
          <div class="button-close__app">
            <button class="button-close" @click="closeCart">X</button>
          </div>
          <li v-for="item in cart" :key="item.id">
            {{ item.title }} - {{ item.count }}
            <button @click="removeCart(item.id)">Удалить</button>
          </li>
          <p class="list-sum">{{ 'Сумма к оплате: ' + sumPurchase() }}</p>
        </ul>
      </cart-alert>
      <!-- </teleport> -->
    </section>
  </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faChevronDown, faShoppingCart } from '@fortawesome/free-solid-svg-icons';

library.add(faChevronDown, faShoppingCart);

import Tabs from './components/Tabs.vue';
import Tab from './components/Tab.vue';
import HomeResources from './components/HomeResources.vue';
import CartAlert from './components/CartAlert.vue';
import CategoryResources from './components/CategoryResources.vue';
import TheProduct from './components/TheProduct.vue';

export default {
  name: 'App',
  components: {
    Tabs,
    Tab,
    FontAwesomeIcon,
    CartAlert,
    CategoryResources,
    HomeResources,
    TheProduct
  },
  data() {
    return {
      myIcon: faShoppingCart,
      cartActive: false,
      cart: [],
      productArr: [],
      url: 'https://fakestoreapi.com/products',
      selected: '',
      arrCategory: [],
      mainArrCategory: [],
      cartProduct: {},
      cartProdCat: {},
      sum: null,
      details: {},
    }
  },
  mounted() {
    if (localStorage.getItem('cart')) {
    try {
        this.cart = JSON.parse(localStorage.getItem('cart'));
    } catch(e) {
        localStorage.removeItem('cart');
      }
    }
  },
  provide() {
    return {
      productFetch: this.productFetch,
      productInCart: this.addCart,
      deleteInCart: this.removeCart,
      activeBtn: this.activeButton,
      moreDetails: this.moreDetails
    }
  },
  methods: {
    closeCart() {
      return this.cartActive = !this.cartActive;
    },
    async productFetch() {
        const res = await fetch(this.url);
        const data = await res.json();
        this.productArr = data;
        this.addProp(this.productArr)
        console.log(this.productArr);
    },
    addProp(arr) {
        for (let i = 0; i < arr.length; i++) {
        this.$set(arr[i], 'active', true);
        this.$set(arr[i], 'count', null);
        }
    },
    addCart(id, arr) {
      console.log(id);
      this.cartProduct = arr.find((prod) => prod.id === id);
      this.cartProduct.active = false;
      if (this.details.id === id) {
        this.details.active = false
      }
      this.cart.push(this.cartProduct);
      console.log(this.cart);
      console.log(arr);
      this.saveCart();
    },
    saveCart() {
      const parsed = JSON.stringify(this.cart);
      localStorage.setItem('cart', parsed);
    },
    removeCart(id) {
      console.log(id);
      this.cartProdCat = this.mainArrCategory.find((prod) => prod.id === id);
      if (this.cartProdCat !== undefined) {
        this.cartProdCat.active = true;
      }
      if (this.cartProdCats === undefined) {
        this.cartProduct = this.productArr.find((prod) => prod.id === id);
        this.cartProduct.active = true;
      }
      if (this.details.id === id) {
        this.details.active = true
      }
      this.cart = this.cart.filter((item) => item.id !== id);
      console.log(this.cart);
      this.saveCart();
    },
    sumPurchase() {
      let initialValue = 0;
      this.sum = this.cart.reduce((accumulator, currentValue) => {
        return accumulator + currentValue.price;
      }, initialValue);
      return '$' + this.sum;
    },
    // category
    async chengeCategory(e) {
        const res = await fetch(this.url + '/category/' + e.target.value);
        console.log(res);
        const data = await res.json();
        this.mainArrCategory = data;
        this.addProp(this.mainArrCategory);
        console.log(this.mainArrCategory);
    },
    async fetchCategory() {
        const res = await fetch(this.url + '/categories');
        console.log(res);
        const data = await res.json();
        this.arrCategory = data;
        console.log(this.arrCategory);
    },
    // product
    async moreDetails(e) {
        const res = await fetch(this.url + '/' + e);
        const data = await res.json();
        this.details = data;
        this.$set(this.details, 'active', true);
        console.log(this.details);
        this.$refs.tabsId.selectTab(2);
    }
  }
}
</script>

