<template>
    <li class="list-item">
        <div class="img-app">
            <img :src="img" alt="image" width="100%" height="100%">
        </div>
        <p>{{ '$' + price }}</p>
        <h3>{{ title }}</h3>
        <p>{{ descriptionLimited }}</p>
        <div class="button-app">
            <button class="button button-details" @click="moreDetails(id)">Подробнее</button>
            <button v-if="active === true" class="button button-cart" @click="productInCart(id, mainArrCategory)">В корзину</button>
            <button v-else-if="active === false" class="button button-remove" @click="deleteInCart(id)">Удалить</button>
        </div>
    </li>
</template>

<script>
export default {
    props: [
    'id',
    'price',
    'category',
    'description',
    'img',
    'title',
    'active',
    'mainArrCategory'
    ],
    inject: ['productInCart', 'deleteInCart', 'moreDetails'],
    computed: {
        descriptionLimited() {
            if (this.description.length > 185) {
                return this.description.slice(0,185) + '...';
            } else {
                return this.description;
            }
        },
    },
}
</script>
